/*Переменные*/
.header {
  background-color: rgba(0, 0, 0, 0.5);
  width: 100vw;
  max-width: 100%;
  padding: 20px;
  z-index: 101; }
  .header--front {
    position: absolute;
    top: 0;
    left: 0; }

.header__navigation,
.social-buttons {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center; }

.header__navigation {
  width: 80%;
  margin: 0 auto; }
  .header__navigation--nojs .popap-menu {
    visibility: visible; }

@media screen and (min-width: 500px) {
  .header__navigation {
    max-width: 80%; } }

.menu {
  margin: 0;
  padding: 0;
  color: #fff;
  font-size: 20px;
  text-align: center; }

.menu__item {
  display: inline;
  list-style: none;
  margin: 5px 10px; }
  .menu__item--open .popap-menu {
    visibility: visible; }

.menu__link {
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  font-weight: bold;
  padding: 5px 10px; }

.menu__link:hover,
.menu__link:focus,
.menu__link:active,
.menu__link.active {
  color: "Lobster", cursive; }

@media (min-width: 992px) {
  .main {
    height: 100vh;
    overflow: hidden; }
  .main_wrapper {
    width: 100%;
    height: 100%;
    position: relative;
    top: 0;
    transition: top 1s; }
  .section {
    width: 100%;
    height: 100%;
    position: relative; } }

.social-buttons {
  width: 120px;
  align-items: middle; }

@media screen and (min-width: 768px) {
  .social-buttons {
    width: 150px; } }

.social-buttons__item {
  width: 30px;
  height: auto;
  list-style: none; }

.social-buttons__item svg {
  fill: #fff; }

.social-buttons__item:hover svg,
.social-buttons__item:focus svg,
.social-buttons__item:active svg {
  fill: #963272; }

#contacts .social-buttons {
  margin: 10px auto;
  width: 180px; }

#contacts .social-buttons__item svg {
  width: 30px;
  height: 30px; }

#contacts .social-buttons_item svg:hover,
#contacts .social-buttons_item svg:focus,
#contacts .social-buttons_item svg:active {
  fill: #963272; }

#about {
  background: grey url("../img/05.jpg") no-repeat top right/cover;
  text-align: left;
  padding-left: 15%; }

#about h1 {
  font-family: 'Lobster', cursive;
  font-size: 7em;
  padding-top: 15%;
  color: #e11a2d;
  text-shadow: 0 0 6px #1e1d19; }

#about p {
  font-size: 3em;
  text-transform: uppercase;
  color: #0b0b0a;
  text-shadow: 0 0 5px #686363;
  width: 50%;
  text-align: center; }

#contacts {
  background: #eeece6 url("../img/04.jpg") no-repeat center/cover;
  text-align: center;
  font-size: 2em;
  padding-top: 120px;
  position: relative; }

.contacts_wrapper {
  position: absolute;
  top: 120px;
  left: 10%;
  width: 50%;
  color: #fff; }

.contacts_wrapper a {
  color: #fff; }

#contacts h2 {
  font-size: 3em;
  font-weight: bold;
  text-transform: uppercase; }

.messangers {
  display: flex;
  justify-content: space-between;
  width: 150px;
  margin: 10px auto; }

.messangers_item {
  width: 40px;
  height: 40px; }

#gallery {
  background: #eeece6 url("../img/03.jpg") no-repeat left center/cover;
  overflow: hidden;
  position: relative; }

.slider__gallery {
  position: absolute;
  top: 35%;
  left: 0;
  bottom: auto; }

#gallery .owl-theme .owl-dots .owl-dot span {
  width: 20px;
  height: 20px;
  background: #E3BDDF; }

#gallery .owl-theme .owl-dots .owl-dot.active span,
#gallery .owl-theme .owl-dots .owl-dot:hover span {
  background: #963272; }

#gallery .owl-prev,
#gallery .owl-next {
  position: absolute;
  top: 35%;
  font-size: 84px;
  color: #fff;
  font-weight: bold; }

#gallery .owl-nav .owl-prev:focus,
#gallery .owl-nav .owl-prev:focus,
#gallery .owl-nav .owl-prev:active,
#gallery .owl-nav .owl-next:focus,
#gallery .owl-nav .owl-next:focus,
#gallery .owl-nav .owl-next:active,
#gallery .owl-theme .owl-nav [class*="owl-"]:hover {
  background: transparent;
  color: #963272; }

#gallery .owl-prev {
  left: 2%; }

#gallery .owl-next {
  right: 2%; }

#reviews {
  background: #eeece6 url("../img/01.jpg") no-repeat left center/cover;
  position: relative; }

#reviews__slider {
  position: absolute;
  bottom: 10%;
  left: 10%;
  width: 80%; }

.reviews-item {
  background-color: #fff;
  padding: 20px;
  box-shadow: 0 0 5px 6px #fff;
  text-align: center; }

.reviews__image {
  border-radius: 50%;
  margin-right: 20px; }

.slider .prev,
.slider .next {
  cursor: pointer;
  position: absolute;
  bottom: 50%;
  width: auto;
  margin-top: -30px;
  padding: 16px;
  color: black;
  font-weight: bold;
  font-size: 18px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0; }

.slider .next {
  right: 5%;
  border-radius: 3px 0 0 3px; }

.slider .prev {
  left: 5%; }

/* При наведении на кнопки добавляем фон кнопок */
.slider .prev:hover,
.slider .next:hover {
  background-color: rgba(0, 0, 0, 0.8);
  color: #fff; }

/* Кружочки */
.slider-dots {
  text-align: center;
  position: absolute;
  width: 100%;
  bottom: 5%; }

.slider-dots_item {
  cursor: pointer;
  height: 12px;
  width: 12px;
  margin: 0 2px;
  background-color: #ddd;
  border-radius: 50%;
  display: inline-block;
  transition: background-color 0.6s ease; }

.active,
.slider-dots_item:hover {
  background-color: #aaa; }

/* Анимация слайдов */
.slider .item {
  animation-name: fade;
  animation-duration: 1.5s; }

@-webkit-keyframes fade {
  from {
    opacity: .4; }
  to {
    opacity: 1; } }

@keyframes fade {
  from {
    opacity: .4; }
  to {
    opacity: 1; } }

#services {
  background: #eeece6 url("../img/02.jpg") no-repeat top left/cover;
  position: relative; }

#services h2 {
  font-size: 10vh;
  text-shadow: 0 0 5px white;
  text-transform: uppercase; }

.services__wrapper {
  position: absolute;
  top: 120px;
  right: 10%;
  width: 50%; }

.services__list {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: flex-start; }

.services__more {
  display: none; }

.services__item {
  width: 30%;
  list-style: none;
  text-align: center;
  margin-top: 4vh;
  padding: 10px;
  color: #fff; }
  .services__item:hover, .services__item:focus, .services__item:active {
    box-shadow: 0 0 5px 3px rgba(0, 0, 0, 0.5);
    background-color: rgba(255, 255, 255, 0.6); }
  .services__item:hover .services__more,
  .services__item:focus .services__more,
  .services__item:active .services__more {
    display: block; }

.services__title {
  font-size: 2vh;
  text-transform: uppercase;
  text-align: center; }

.services__image {
  height: 25vh;
  width: auto;
  max-width: 100%; }

#team {
  background: #eeece6 url("../img/IMG_2215.jpg") no-repeat top right/cover; }

.team__list {
  margin: 0;
  padding-top: 200px;
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: flex-start; }

.team__item {
  width: 45%;
  padding: 15px;
  list-style: none;
  position: relative;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  margin-bottom: 2vh;
  z-index: 0; }

.team__image {
  display: block;
  width: 50%;
  display: inline-block; }

.team__header {
  text-align: center;
  font-size: 4vh;
  display: inline-block;
  vertical-align: top;
  width: 45%; }

.team__text {
  position: absolute;
  top: -22px;
  left: 0;
  width: 100%;
  min-height: 100%;
  height: auto;
  opacity: 0;
  padding: 5%;
  background: #000;
  text-align: center;
  font-size: 24px;
  z-index: 10; }
  .team__text:hover, .team__text:focus {
    opacity: 1;
    transition: 1s; }

